import api from './api.js'
import ajax from './ajax.js'


export default {
	//帮助中心接口
	helpCenter:function(data,fn){
		ajax.get(api.helpCenter,data,fn);
	},
	//帮助分类
	helpClassify:function(data,fn){
		ajax.get(api.helpClassify,data,fn);
	},
	//帮助分类
	helpCenterClassify:function(data,fn){
		ajax.post(api.helpClassify,data,fn);
	}
}


